var Unexpected = require('unexpected');
var UnexpectedReact = require('../unexpected-react');

var React = require('react');
var ReactTestRenderer = require('react/lib/ReactTestRenderer');
var TestUtils = require('react-addons-test-utils');
var Immutable = require('immutable');

var PropTypes = React.PropTypes;

var expect = Unexpected.clone()
    .installPlugin(UnexpectedReact);

var ES5Component = React.createClass({
    displayName: 'ES5Component',
    render() { return null;}
});


function createNoNameComponent() {
    return React.createClass({
        displayName: '',
        render() { return null;}
    });
}

var NoNameComponent = createNoNameComponent();

class ClassComponent extends React.Component {
    render() {
        return (
            <div className="class-component">
                {this.props.content}
            </div>
        );
    }
}

ClassComponent.propTypes = {
    content: PropTypes.any
};

class MyDiv extends React.Component {
    render() {
        return React.createElement('div', this.props);
    }
}

const FunctionComp = function (props) {
    return (<div {...props} />);
};

describe('unexpected-react test renderer', () => {

    it('renders', () => {
        const renderer = ReactTestRenderer.create(<div className="foo"><span>one</span><span>two</span></div>);

        expect(renderer, 'to be a', 'ReactTestRenderer');
    });
});
